.App {
    background-color: #111111;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.logo-container {
    text-align:center;
    height: 10em;
    margin: auto;
    padding-top: 3vh;
    padding-bottom:9.5vh;
    border: 1px solid white;
}

.App-logo {
    margin: auto;
    padding: 2em;
    min-height:100%;
    max-width: 100%;
    max-height: 100%;
    -webkit-transform: rotate(-45deg) scale(1.33);
        -ms-transform: rotate(-45deg) scale(1.33);
            transform: rotate(-45deg) scale(1.33);
    #border:1px solid white;
  #animation: App-logo-spin infinite 20s linear;

  height: 80px;
  text-align: center;

}

.App-header {
  background-color: #222;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px;
  color: white;
}

a:link {
    color: white;
}

.App-title {
  font-size: 5em;
}

.links{
    font-align: left;
    font-size: 2em;
}

.App-intro {
  font-size: large;
}

.outerContainer {
    margin: auto;
    width:90%;
    background-color: #333333;
    border-radius: 1em;
    #display: inline-block;
}

.container {
    margin: auto;
    width:20%;
    background-color: #444444;
    color: aliceblue;
    border-radius: 1em;
    display: inline-block;
    margin: 1em;
}

.contentcontainer {
    margin: 1em;
    border-radius: 1em;
    width: 80%;
    max-width: inherit;
    background-color: #111111;
}

.post {
    border: 1px solid antiquewhite;
    width: 80%;
    padding: 1em;
    border-radius: 1em;
    margin-bottom: 1em;
    margin-top: 1em;
    color: aliceblue;
}

.postimg {
    max-width: 80%;
    width: 80%;
    border-radius:.8em;
}

.dot {
    border-radius: 1em;
    border: 1em solid aliceblue;
    width:1px;
    display: -ms-inline-flexbox;
    display: inline-flex;
    position:relative;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}

.fadein-appear {
    opacity: 0.01;
}

.fadein-appear.fadein-appear-active {
    opacity: 0.99;
    -webkit-transition: opacity 2.5s ease-in;
    -o-transition: opacity 2.5s ease-in;
    transition: opacity 2.5s ease-in;
}

.wobble-appear {
    -webkit-transition: transformX;
    -o-transition: transformX;
    transition: transformX
}



